@-webkit-keyframes newMemo {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      opacity: 1;
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes newMemo {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
